	/**
	 * get calendar type
	 * @param value 
	 */
	calendarType(value) {
		this.cdr.detectChanges();
		this.calenderMonthformat = "";
		this.calenderYearformat = "";
		this.yearformat = "";
		this.monthformat = "";
		if (value == 'CALENDAR') {
			this.isCalendar = true
		}
		else {
			this.isCalendar = false
		}

	}

	/**
	 * get Invoice number
	 */
	getInvoiceNumber() {

		this.saleInvoiceService.getInvoiceNumber({}, this.currentUser.id).then((res) => {
			if (res) {
				this.isShowButton = true
			}
			this.FormatId = res.data.id
			if (res.data.calendarType == 'CALENDAR') {
				this.isCalendar = true;
			} else {
				this.isCalendar = false
			}
			if (res.data.year == true) {
				this.isShown = true;
			} else {
				this.isShown = false
			}
			if (res.data.month == true) {
				this.isShownm = true;
			} else {
				this.isShownm = false
			}
			this.NumberForm.patchValue({
				invoiceNo: res.data.invoiceNo,
				calendarType: res.data.calendarType,
				prefixOne: res.data.prefixOne,
				prefixTwo: res.data.prefixTwo,
				seprator1: res.data.seprator1,
				year: res.data.year,
				month: res.data.month,
				yearformat: res.data.yearformat,
				monthformat: res.data.monthformat,
				seprator2: res.data.seprator2,
				numberOfZeros: res.data.numberOfZeros,
				lastDigit: res.data.lastDigit,
			})
			this.termsAndConditions = res.data[0].termsAndConditions;
			this.invoiceTermsCon=localStorage.getItem('termsAndConditions');
			this.lastDigit = res.data.lastDigit
			this.calenderYearformat = res.data.yearformat
			if (this.calenderYearformat == "YYYY-YYYY") {
				this.yearformat = this.financialYear
			} else if (this.calenderYearformat == "YYYY") {
				this.yearformat = this.currentYear;
			} else if (this.calenderYearformat = "YY-YY") {
				this.yearformat = this.twoDigitFinancialYear + "-" + (this.twoDigitFinancialYear + 1)
			}
			this.calenderMonthformat = res.data.monthformat
			if (this.calenderMonthformat == "MMM") {
				this.monthformat = this.monthShortName
			} else if (this.calenderMonthformat == "MM") {
				this.monthformat = this.monthNumber
			}
		}, (error) => {
			if (error.error.status == 404) {
				this.isShowButton = false
			}
		})
		//this.cdr.detectChanges(); 
	}

	/**
	 * get Invoice setting
	 */
	getInvoicesetting() {

		this.saleInvoiceService.getInvoicesetting({}, this.currentUser.id).then((res) => {
			if (res) {
				this.listdata = res.data;
				if (this.listdata.length > 0) {
					this.isShowButtonsetting = true;
					console.log("patch value kar be");
					this.reciptform.patchValue({
						companytextsize: res.data[0].companyNameTextSize,
						printextsize: res.data[0].printTextSize,
						termsandcondition: res.data[0].termsAndConditions,

					})
					this.isActiveCompanyName = res.data[0].companyName;
					this.isActiveCompanyLogo = res.data[0].companyLogo;
					this.isActiveCompanyAddress = res.data[0].address;
					this.isActiveCompanyEmail = res.data[0].email;
					this.isActiveCompanyNumber = res.data[0].phoneNumber;
					this.selectedColor = res.data[0].color;
					this.termsAndConditions = res.data[0].termsAndConditions;

					// Replace unwanted characters before storing in localStorage
					const sanitizedTerms = this.termsAndConditions.replace(/["\n]/g, '');
					localStorage.setItem('termsAndConditions', sanitizedTerms);;

					this.invoiceTermsCon=localStorage.getItem('termsAndConditions');
					
				}
				else {
					console.log("data not save")
					this.isShowButtonsetting = false
				}
			}



		}, (error) => {
			if (error.error.status == 404) {
				this.isShowButtonsetting = false
			}
		})
		//this.cdr.detectChanges(); 
	}
	/**
	 * get & set no of zeroes
	 * @param event 
	 */
	getNoOfZeros(event) {

		if (event == 1) {
			this.numberOfZeros = "0";
		} else if (event == 2) {
			this.numberOfZeros = "00";
		} else if (event == 3) {
			this.numberOfZeros = "000";
		} else if (event == 4) {
			this.numberOfZeros = "0000";
		} else if (event == 5) {
			this.numberOfZeros = "00000";
		} else if (event == 6) {
			this.numberOfZeros = "000000";
		} else if (event == 7) {
			this.numberOfZeros = "0000000";
		} else if (event == 8) {
			this.numberOfZeros = "00000000";
		}
		this.cdr.detectChanges();
		console.log("Number of zeroes", this.numberOfZeros)
	}

	getMonthFormat(monthType) {

		if (monthType == "MMM") {
			this.monthformat = this.monthShortName
		} else if (monthType == "MM") {
			this.monthformat = this.monthNumber
		}
		this.cdr.detectChanges();
	}
	getYearFormat(yearType) {

		if (yearType == "YYYY-YYYY") {
			this.yearformat = this.financialYear
		} else if (yearType == "YYYY") {
			this.yearformat = this.currentYear;
		} else if (yearType = "YY-YY") {
			this.yearformat = this.twoDigitFinancialYear + "-" + (this.twoDigitFinancialYear + 1)
		}
		this.cdr.detectChanges();
	}
